@use 'base/variables' as v;

.header {
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: v.$z-fixed;
	background-color: v.$black;
	transition: background-color .3s;

	&:hover {
		background-color: v.$white;
		box-shadow: 0 3px 5px 0.3px rgba(0, 0, 0, 0.1);
		.nav__logo{
			color: v.$black;
		}

	}
	&.inverted {
		background-color: v.$white;
		box-shadow: 0 3px 5px 0.3px rgba(0, 0, 0, 0.1);
	}
}