@use 'base/variables' as v;
@use 'base/mixins' as m;

.slogan  {
  background-color: v.$black;
  color: v.$white;
  padding: 8rem 2rem;
  h2 {
    margin: 0;
    font-size: 2.5rem;

    @include m.tablet {
        font-size: 5rem;
    }

  }

  .btn {
    @include m.btn(v.$green,v.$white);
    margin: 2rem 0 0;
  }

  .slogan__container {
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  @include m.tablet {
    padding: 8rem 0;
  }
}