@use 'base/variables' as v;
@use 'base/mixins' as m;

.mission-vision__container {
  display: flex;
  flex-direction: column;
  text-align: center;

  .mission {
    padding: 2rem;
    text-align: left;
  }

  .vision {
    padding: 2rem;
    text-align: right;
  }

  @include m.tablet {
    flex-direction: row;
    padding: 0;
  }
}