html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

* {
  padding: 0;
  margin: 0;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto Condensed", sans-serif;
  font-size: 2rem;
}

p {
  font-size: 2rem;
  color: #000;
}

.container {
  width: 100%;
  max-width: 160rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 80%;
  }
}

.section {
  padding: 8rem 0 0;
}

.grid {
  display: grid;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0 0 2.5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.text-center {
  text-align: center;
}

.bg-orange {
  background-color: #E07A5F;
}

.bg-yellow {
  background-color: #F2CC8F;
}

.bg-blue {
  background-color: #3D405B;
}

.bg-white-light {
  background-color: #F4F1DE;
}

.uppercase {
  text-transform: uppercase;
}

.box-shadow {
  box-shadow: 0 1.5rem 2rem 0rem rgba(0, 0, 0, 0.1);
}

.border-gray {
  border: 0.1rem solid rgb(208, 208, 208);
}

.p-2 {
  padding: 2rem;
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: #000;
  transition: background-color 0.3s;
}
.header:hover {
  background-color: #fff;
  box-shadow: 0 3px 5px 0.3px rgba(0, 0, 0, 0.1);
}
.header:hover .nav__logo {
  color: #000;
}
.header.inverted {
  background-color: #fff;
  box-shadow: 0 3px 5px 0.3px rgba(0, 0, 0, 0.1);
}

.nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
}
.nav .nav__menu .nav__close {
  display: none;
}
.nav .nav__logo {
  font-size: 3.5rem;
  font-weight: 400;
  color: #fff;
  background-color: #F4F1DE;
  width: 8rem;
  height: 6rem;
  line-height: 5rem;
  color: #fff;
  text-align: center;
  transition: all 0.5s ease;
  color: #000;
  /* clip-path: polygon(0% 0%, 100% 0, 100% 75%, 60% 75%, 30% 100%, 30% 75%, 0% 75%); */
  /* clip-path: polygon(50% 0%, 83% 12%, 100% 43%, 94% 78%, 68% 100%, 32% 100%, 6% 78%, 0% 43%, 17% 12%); */
  clip-path: polygon(25% 0%, 75% 0%, 100% 35%, 50% 100%, 0% 35%);
}
.nav .nav__logo:hover {
  color: #fff;
  background-color: #000;
}
.nav .nav__list {
  display: flex;
  flex-direction: column;
  list-style: none;
  gap: 0.5rem;
  padding: 0.5rem;
}
@media (min-width: 768px) {
  .nav .nav__list {
    flex-direction: row;
    gap: 2.5rem;
  }
}
.nav .nav__link {
  display: flex;
  gap: 0.5rem;
  font-size: 2rem;
  color: rgb(78, 78, 78);
  font-weight: 400;
}
.nav .nav__inverted {
  color: #000;
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    flex-direction: row;
    padding: 2.5rem 0;
  }
}

.slogan {
  background-color: #000;
  color: #fff;
  padding: 8rem 2rem;
}
.slogan h2 {
  margin: 0;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .slogan h2 {
    font-size: 5rem;
  }
}
.slogan .btn {
  background-color: #81B29A;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 2rem 0 0;
}
@media (min-width: 768px) {
  .slogan .btn {
    width: auto;
  }
}
.slogan .btn:hover {
  background-color: #619f81;
  cursor: pointer;
}
.slogan .slogan__container {
  display: flex;
  flex-direction: column;
  text-align: center;
}
@media (min-width: 768px) {
  .slogan {
    padding: 8rem 0;
  }
}

.about__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about__container .information {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 90vw;
  margin: 3rem 0;
}
.about__container .information .text__description {
  margin-bottom: 2.5rem;
  text-align: center;
}
.about__container .information .text__description p {
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .about__container .information .text__description p {
    font-size: 3rem;
  }
}
.about__container .information h2 {
  text-align: center;
}
.about__container .information__team {
  display: flex;
  flex-direction: column;
}
.about__container .information__team .image__responsive {
  width: 20rem;
  height: 20rem;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .about__container .information__team .image__responsive {
    width: 35rem;
    height: 35rem;
  }
}
.about__container .information__team .container__person {
  width: 90vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 0 2rem;
  padding: 2rem;
  background-color: #F4F1DE;
}
.about__container .information__team .container__person .person-image {
  margin: 0 0 2rem 0;
}
@media (min-width: 768px) {
  .about__container .information__team .container__person .person-image {
    margin: 0 2rem 0 0;
  }
}
.about__container .information__team .container__person .person-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
}
@media (min-width: 768px) {
  .about__container .information__team .container__person .person-info {
    text-align: left;
  }
}
@media (min-width: 768px) {
  .about__container .information__team .container__person {
    width: 70vw;
  }
}
@media (min-width: 1024px) {
  .about__container .information__team .container__person {
    width: 80vw;
    flex-direction: row;
    justify-content: center;
    clip-path: polygon(10% 0, 90% 0, 100% 50%, 90% 100%, 10% 100%, 0% 50%);
  }
}
@media (min-width: 480px) {
  .about__container .information__team {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
.about__container .btn {
  background-color: #000;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .about__container .btn {
    width: auto;
  }
}
.about__container .btn:hover {
  background-color: black;
  cursor: pointer;
}

.home {
  padding: 8rem 0 0;
}

.home__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.home__container .image__text {
  position: absolute;
  top: 38%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.home__container .image__text h2 {
  color: #fff;
}
@media (min-width: 768px) {
  .home__container .image__text {
    top: 50%;
  }
}

.slogan__button {
  background-color: #000;
  color: #fff;
  padding: 8rem 0;
}
.slogan__button h2 {
  margin: 0;
}
.slogan__button .btn {
  background-color: #81B29A;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 2rem 0 0;
}
@media (min-width: 768px) {
  .slogan__button .btn {
    width: auto;
  }
}
.slogan__button .btn:hover {
  background-color: #619f81;
  cursor: pointer;
}
.slogan__button .slogan__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mission-vision__container {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.mission-vision__container .mission {
  padding: 2rem;
  text-align: left;
}
.mission-vision__container .vision {
  padding: 2rem;
  text-align: right;
}
@media (min-width: 768px) {
  .mission-vision__container {
    flex-direction: row;
    padding: 0;
  }
}

.services .services__content {
  margin: 0 0 6rem 0;
}
.services .services__content h3 {
  font-size: 3rem;
}
@media (min-width: 768px) {
  .services .services__content h3 {
    font-size: 6rem;
  }
}
.services .services__list {
  display: grid;
  grid-template-columns: 1fr;
  padding: 2.5rem 0;
  row-gap: 3rem;
  background-color: #3D405B;
}
.services .services__list .item__link {
  display: flex;
  flex-direction: column;
  text-align: center;
  color: #fff;
  transition: transform 0.5s;
}
.services .services__list .item__link .uil {
  font-size: 5rem;
}
.services .services__list .item__link:hover {
  transform: scale(1.1);
}
@media (min-width: 768px) {
  .services .services__list {
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-around;
    padding: 5rem 0;
  }
}

.tools .tools__content {
  margin: 0 0 6rem 0;
}
.tools .tools__content h3 {
  font-size: 4rem;
}
@media (min-width: 768px) {
  .tools .tools__content h3 {
    font-size: 6rem;
  }
}
.tools .tools__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 2.5rem 0;
  row-gap: 3rem;
  background-color: #81B29A;
}
.tools .tools__list .item__link {
  display: flex;
  flex-direction: column;
  text-align: center;
  color: #fff;
  transition: transform 0.5s;
}
.tools .tools__list .item__link .uil {
  font-size: 5rem;
}
.tools .tools__list .item__link:hover {
  transform: scale(1.1);
}
@media (min-width: 768px) {
  .tools .tools__list {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 5rem 0;
  }
}

.comments {
  padding: 8rem 0;
}
.comments .btn {
  background-color: #000;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .comments .btn {
    width: auto;
  }
}
.comments .btn:hover {
  background-color: black;
  cursor: pointer;
}
.comments form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 8rem;
}
.comments form .form__info {
  padding: 2rem;
}
@media (min-width: 768px) {
  .comments form {
    flex-direction: row;
  }
}
.comments .form__info input {
  border: none;
  background: transparent;
  width: 70%;
  border-bottom: 0.1rem solid rgb(78, 78, 78);
  font-size: 1.8rem;
  margin-bottom: 1rem;
  outline: none;
}
.comments .form__info input::placeholder {
  color: rgb(208, 208, 208);
}
.comments .comments__container {
  margin: 4rem 0 0;
}
.comments .comments__container .comment__card {
  width: 100%;
  height: 14rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 3rem;
  border-bottom: 1px solid rgb(78, 78, 78);
}
.comments .comments__container .comment__card .pic {
  width: 5rem;
  height: auto;
  background-color: #3D405B;
  border-radius: 50%;
  font-size: 3rem;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  margin: 0 2rem;
}
@media (min-width: 768px) {
  .comments .comments__container .comment__card .pic {
    font-size: 5rem;
  }
}
.comments .comment__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.comments .comment__info .nickname {
  color: rgb(78, 78, 78);
}
.comments .comment__info .comment {
  margin: 0.5rem 0;
}
.comments .comment__info .comment__bottom {
  display: flex;
}

.footer {
  background-color: #000;
  color: #fff;
  padding: 8rem;
}
.footer .footer-row {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .footer .footer-row {
    flex-direction: row;
  }
}
.footer .footer-row .footer-col {
  width: 100%;
  margin: 2rem;
}
.footer .footer-row .footer-col h4 {
  text-transform: capitalize;
  margin: 0 0 3rem;
  position: relative;
}
.footer .footer-row .footer-col h4::before {
  content: "";
  position: absolute;
  bottom: -1rem;
  background-color: #E07A5F;
  height: 0.2rem;
  width: 5rem;
}
.footer .footer-row .footer-col ul li a {
  font-size: 1.6rem;
  text-transform: capitalize;
  color: rgb(78, 78, 78);
  font-weight: 300;
  transition: all 0.5s ease;
}
.footer .footer-row .footer-col ul li a:hover {
  color: #fff;
  padding-left: 2rem;
}
.footer .footer-row .footer-col ul li:not(:last-child) {
  margin-bottom: 1rem;
}
.footer .footer-row .footer-col .social-links a {
  display: inline-block;
  width: 4rem;
  background-color: rgb(78, 78, 78);
  margin: 0 1rem 1rem 0;
  line-height: 4rem;
  border-radius: 50%;
  color: #fff;
  text-align: center;
  transition: all 0.5s ease;
}
.footer .footer-row .footer-col .social-links a:hover {
  color: #000;
  background-color: #fff;
}