// Typography
$font-family: 'Roboto Condensed', sans-serif;

// Colors
$black: #000;
$white: #fff;
$white-light: #F4F1DE;
$yellow: #F2CC8F;
$green: #81B29A;
$orange: #E07A5F;
$blue: #3D405B;
$gray: rgba(78, 78, 78, 1.0);
$gray-ligth: rgb(208, 208, 208, 1.0);

// Font-colors
$hue: 0;
$sat: 0%;
$title-color: hsl(var($hue),var($sat),20%);
$title-color-dark: hsl(var($hue),var($sat),0%);

// font-sizes
$big-font-size: 3.5rem;
$h1-font-size: 4rem;
$h2-font-size: 3.6rem;
$h3-font-size: 3rem;
$p-font-size: 2rem;
$normal-font-size: 2rem;
$small-font-size: 1.5rem;
$smaller-font-size: 1rem;
$tiny-font-size: 0.625rem;
$font-normal: 300;
$font-medium: 400;
$font-semi-bold: 700;

// utilities
$header-height: 6.5rem;
$container-gutter: 16rem;

// order
$z-tooltip: 10;
$z-fixed: 100;
$z-modal: 1000;

// size of Media Queries
$phone: 480px;
$tablet: 768px;
$desktop: 1024px;