@use 'base/variables' as v;

.text-center {
  text-align: center;
}

.bg-orange {
  background-color: v.$orange;
}

.bg-yellow {
  background-color: v.$yellow;
}

.bg-blue {
  background-color: v.$blue;
}

.bg-white-light {
  background-color: v.$white-light;
}

.uppercase {
  text-transform: uppercase;
}

.box-shadow {
  box-shadow: 0 1.5rem 2rem 0rem rgba(0,0,0,.1)
}

.border-gray {
  border: .1rem solid v.$gray-ligth;
}

.p-2 {
  padding: 2rem;
}