@use 'base/variables' as v;
@use 'base/mixins' as m;

.home {
  padding: 8rem 0 0;
}

.home__container {
  display: flex;
  flex-direction: column;
  align-items: center;

  .image__text {
    position: absolute;
    top: 38%;
    left: 50%;
    transform: translate(-50%, -50%);

    h2 {
      color: v.$white;
    }
    @include m.tablet {
      top: 50%;
    }
  }
}