@use 'base/variables' as v;
@use 'base/mixins' as m;

.nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: .5rem;

  .nav__menu {
		.nav__close { // check later for mobile
			display: none;
		}
	}

  .nav__logo {
    font-size: v.$big-font-size;
    font-weight: v.$font-medium;
    color: v.$white;
    background-color: v.$white-light;
    width: 8rem;
    height: 6rem;
    line-height: 5rem;
    
    color: v.$white;
    text-align: center;
    transition: all .5s ease;
    color: v.$black;
    /* clip-path: polygon(0% 0%, 100% 0, 100% 75%, 60% 75%, 30% 100%, 30% 75%, 0% 75%); */
    /* clip-path: polygon(50% 0%, 83% 12%, 100% 43%, 94% 78%, 68% 100%, 32% 100%, 6% 78%, 0% 43%, 17% 12%); */
    clip-path: polygon(25% 0%, 75% 0%, 100% 35%, 50% 100%, 0% 35%);

    &:hover {
      color: v.$white;
      background-color: v.$black;
    }
  }

  .nav__list {
    display: flex;
    flex-direction: column;
    list-style: none;
    gap: .5rem;
    padding: .5rem;
    
    @include m.tablet {
      flex-direction: row;
      gap: 2.5rem;
    }
  } 

  .nav__link {
    display: flex;
    gap: .5rem;
    font-size: v.$normal-font-size;
    color: v.$gray;
    font-weight: v.$font-medium;
  }

  .nav__inverted {
    color: v.$black;
  }
  
  @include m.tablet {
    display: flex;
    flex-direction: row;
    padding: 2.5rem 0;
  }
}