@use 'base/variables' as v;
@use 'base/mixins' as m;

.comments {
  padding: 8rem 0;

  .btn {
    @include m.btn(v.$black,v.$white);
    margin: 0;
  }

  form {
    display: flex;
    flex-direction: column;
    justify-content: center;  
    align-items: center;
    width: 100%;
    height: 8rem;

    .form__info {
      padding: 2rem;
    }

    @include m.tablet {
      flex-direction: row;
    }
  }

  .form__info {

    input {
      border: none;
      background: transparent;
      width: 70%;
      border-bottom: .1rem solid v.$gray;
      font-size: 1.8rem;
      margin-bottom: 1rem;
      outline: none;

      &::placeholder {
        color: v.$gray-ligth;
      }
    }
  }

  .comments__container {
    margin: 4rem 0 0;

    .comment__card {
      width: 100%;
      height: 14rem;
      display: flex;
      flex-direction: row;
      align-items: center;
      gap: 3rem;
      border-bottom: 1px solid v.$gray;

      .pic {
        width: 5rem;
        height: auto;
        background-color: v.$blue;
        border-radius: 50%;
        font-size: 3rem;
        color: v.$white;
        text-transform: uppercase;
        text-align: center;
        margin: 0 2rem;

        @include m.tablet {
          font-size: 5rem;
        }
        
      }
    }
  }

  .comment__info {
    display: flex;
    flex-direction: column; 
    justify-content: center;

    .nickname {
      color: v.$gray;
    }

    .comment {
      margin: .5rem 0;
    }

    .comment__bottom {
      display: flex;
    }
  }
}
