@use 'base/variables' as v;
@use 'base/mixins' as m;

.slogan__button  {
  background-color: v.$black;
  color: v.$white;
  padding: 8rem 0;
  h2 {
    margin: 0;
  }

  .btn {
    @include m.btn(v.$green,v.$white);
    margin: 2rem 0 0;
  }

  .slogan__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}