@use 'variables' as v;
@use 'mixins' as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

* {
  padding: 0;
  margin: 0;
}

*, 
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: v.$font-family;
  font-size: v.$normal-font-size;
}

p {
  font-size: v.$p-font-size;
  color: v.$black;
}

.container {
  width: 100%; 
  max-width: 160rem;
  margin: 0 auto;

  @include m.tablet {
    width: 80%;
  }
}

.section {
  padding: 8rem 0 0;
}

.grid {
  display: grid;
  grid-gap: 1.5rem;
  gap: 1.5rem
}

a {
  text-decoration: none;
}

h1,
h2, 
h3 {
  margin: 0 0 2.5rem 0;
}

h1 {
  font-size: v.$h1-font-size;
}

h2 {
  font-size: v.$h2-font-size;
}

h3 {
  font-size: v.$h3-font-size;
}

ul {
  list-style: none;
}

img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}

