@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
  background-color: v.$black;
  color: v.$white;
  padding: 8rem;

  .footer-row {
    display: flex;
    flex-direction: column;

    @include m.tablet { 
      flex-direction: row;

    }

    .footer-col {
      width: 100%;
      margin: 2rem;

      h4 {
        text-transform: capitalize;
        margin: 0 0 3rem;
        position: relative;

        &::before {
          content: '';
          position: absolute;
          bottom: -1rem;
          background-color: v.$orange;
          height: .2rem;
          width: 5rem;
        }
      }

      ul li a {
        font-size: 1.6rem;
        text-transform: capitalize;
        color: v.$gray;
        font-weight: v.$font-normal;
        transition: all .5s ease;

        &:hover {
          color: v.$white;
          padding-left: 2rem;
        }
      }

      ul li:not(:last-child) {
        margin-bottom: 1rem;
      }

      .social-links a {
        display: inline-block;
        width: 4rem;
        background-color: v.$gray;
        margin: 0 1rem 1rem 0;
        line-height: 4rem;
        border-radius: 50%;
        color: v.$white;
        text-align: center;
        transition: all .5s ease;

        &:hover {
          color: v.$black;
          background-color: v.$white;
        }
      }
    }
  }
  
}