@use 'base/variables' as v;
@use 'base/mixins'as m;

.about__container {
  display: flex;
  flex-direction: column;
  align-items: center;

    .information {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 90vw;
      margin: 3rem 0;
      

      .text__description {
        margin-bottom: 2.5rem;
        text-align: center;

        p {
          font-size: v.$normal-font-size + .5rem;

          @include m.tablet {
            font-size: v.$normal-font-size + 1rem;
          }
        }
      }
      
      h2 {
        text-align: center;
      }
      
      
    }

    .information__team {
      display: flex;
      flex-direction: column;

      .image__responsive {
        width: 20rem;
        height: 20rem;
        border-radius: 50%;

        @include m.tablet {
          width: 35rem;
          height: 35rem;

        }
      }

      .container__person {
        width: 90vw;
        display: flex; 
        flex-direction: column;
        align-items: center;
        margin: 0 0 2rem;
        padding: 2rem;
        background-color: v.$white-light;

        .person-image {
          margin: 0 0 2rem 0;

          @include m.tablet {
            margin: 0 2rem 0 0;
          }
        }

        .person-info {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          text-align: center;

          @include m.tablet {
            text-align: left;
          }
        }

        @include m.tablet {
          width: 70vw;
        }

        @include m.desktop {
          width: 80vw;
          flex-direction: row;
          justify-content: center;
          clip-path: polygon(10% 0, 90% 0, 100% 50%, 90% 100%, 10% 100%, 0% 50%);
        }
      }

      @include m.phone {
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
    }

    

  .btn {
    @include m.btn(v.$black,v.$white);
    margin: 0;
  }
}