@use 'base/variables' as v;
@use 'base/mixins' as m;

.tools {
  

  .tools__content {
    margin: 0 0 6rem 0;
    
    h3 {
      font-size: 4rem;

      @include m.tablet {
        font-size: 6rem;
      }
    }
  }
  
  .tools__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 2.5rem 0;
    row-gap: 3rem;
    background-color: v.$green;
    
    .item__link {
      display: flex;
      flex-direction: column;
      text-align: center;
      color: v.$white;
      transition: transform .5s;

      .uil {
        font-size: 5rem;
      }

      &:hover {
        transform: scale(1.1);
      }


    }

    @include m.tablet {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      padding: 5rem 0;
    }
  }
}